.App-side {
  background-color: #282c34;
  opacity: 1;
  height: 100vh;
  width: 23vw;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: flex-start;
  font-size: calc(20px + 2.5vmin);
  color: turquoise;
}


.App-side-menu {
  width: 23vw;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  font-size: calc(10px + 1.5vmin);
  color: rgb(45, 182, 175);
}

.menu {
  /* border: 1px solid #fff; */
  height: 50vh;
  width: 9vw;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
  align-content: center;
}

.App-side-close {
  margin: 1em 0em 0em 28em;
}

.button {
  height: 4rem;
  width: 6rem;
  text-align: center;
  z-index: 10;
}

.visible {
  z-index: 110;
}

.hidden {
  display: none;
}



