// GRID
.grid {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
//   border: 1px solid blue;
}

.grid button:nth-child(14) {
  animation: anim 50s linear infinite;
  animation-delay: 0s;
}
.grid button:nth-child(7) {
  animation: anim 50s linear infinite;
  animation-delay: -10s;
}
.grid button:nth-child(3) {
  animation: anim 50s linear infinite;
  animation-delay: -20s;
}
.grid button:nth-child(6) {
  animation: anim 50s linear infinite;
  animation-delay: -30s;
}
.grid button:nth-child(10) {
  animation: anim 50s linear infinite;
  animation-delay: -40s;
}

@keyframes anim {
  20% {
    opacity: 1;
    color: #fff;
  }
  40% {
    opacity: 0;
    color: #fff;
  }
  100% {
    opacity: 0;
    color: #fff;
  }
}



.svg-wrapper {
  height: 60px;
	margin: 0 auto;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  width: 320px;
}

.shape {
  fill: transparent;
  stroke-dasharray: 140 540;
  stroke-dashoffset: -474;
  stroke-width: 8px;
  stroke: #19f6e8;
}

.text {
  color: #fff;
  font-family: 'Roboto Condensed';
  font-size: 22px;
  letter-spacing: 8px;
  line-height: 32px;
  position: relative;
  top: -48px;
}

@keyframes draw {
  0% {
    stroke-dasharray: 140 540;
    stroke-dashoffset: -474;
    stroke-width: 8px;
  }
  100% {
    stroke-dasharray: 760;
    stroke-dashoffset: 0;
    stroke-width: 2px;
  }
}

.svg-wrapper:hover .shape {
  -webkit-animation: 0.5s draw linear forwards;
  animation: 0.5s draw linear forwards;
}

